.invert span {
  border-bottom: 3px solid white;
}

div.cover {
  position: fixed;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
}

img.cover {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  min-width: 50%;
  min-height: 50%;
  overflow-x: hidden;
}

.iframe-embed,
.video {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;

  >iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
  }
}

.patreon-block {
  background: var(--bgp-yellow);
  padding: 20px;

  a {
    box-shadow: none;
  }

  a:hover {
    box-shadow: none;
    background: none;
    animation: none;
  }

  .patreon-block__link {
    width: 200px;
    display: block;
    margin: auto;

    svg {
      width: 100%;
      height: 50px;
    }
  }
}